@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&display=swap);
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  background-color: #333;
}

nav a {
  color: #f040af;
}

.link {
  font-size: 16px;
  padding: 14px 16px;
  text-decoration: none;
  padding: 39px 34px;
}

.subnav {
  overflow: hidden;
}

.subnav .subnavbtn {
  font-size: 16px;
  border: none;
  outline: none;
  padding: 39px 34px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
  color: #f040af;
}

nav a:hover:not(.flex),
.subnav:hover .subnavbtn {
  background-color: #272727;
  color: #f040af;
}

.subnav-content {
  display: none;
  position: absolute;
  background-color: #272727;
  width: 100%;
  left: 0;
  z-index: 1;
}

.subnav-content a {
  float: right;
  color: white;
  text-decoration: none;
  padding: 14px 16px;
  margin: 0 2rem;
}

.subnav-content a:hover {
  background-color: #eee;
  color: black;
}

.subnav:hover .subnav-content {
  display: block;
}

.member {
  box-shadow: 0 0 0.25em 0.25em rgba(0, 0, 0, 0.25);
  width: 39%;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 5px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.member.header {
  box-shadow: unset;
  width: unset;
  cursor: unset;
  margin-bottom: 1%;
}

.member h1 {
  margin: 0;
  cursor: pointer;
}

.member .member-wrapper {
  margin: 0 0 0 1rem;
  width: 100%;
}

.member-wrapper h1 {
  overflow: hidden;
  max-width: 90%;
}

.member-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.member p {
  margin: 2px 0;
  font-size: 16px;
}

.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.tooltip .tooltip-text {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 105%;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
}

.warrior {
  background-color: #c69b6d;
}

.druid {
  background-color: #ff7c0a;
}

.hunter {
  background-color: #aad372;
}

.priest {
  background-color: #ffffff;
}

.warlock {
  background-color: #8788ee;
}

.mage {
  background-color: #3fc7eb;
}

.shaman {
  background-color: #0070dd;
}

.paladin {
  background-color: #f58cba;
}

.icon {
  height: 20px;
  width: 20px;
}

.rogue {
  background-color: #fff468;
}

.team-role {
  color: #f040af;
  padding: 1rem;
  border: 1px solid #f040af;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

i {
  margin-right: 5px;
}

.wrapper .flex {
  flex-wrap: wrap;
  justify-content: center;
}

.class-icon {
  height: 70px;
  margin: auto;
  border-radius: 50%;
}

table {
  width: 99%;
  border-collapse: collapse;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
}

th,
td {
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.2);
  color: black;
}

th {
  text-align: left;
}

thead th {
  background-color: #8936a4;
}

tbody td {
  position: relative;
  padding: 0.5rem 1rem;
}

.player-info i {
  margin: 5px;
}

.player-info {
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 5px;
}

.table-wrapper {
  overflow: auto;
}

.player-wrapper {
  margin: 0 0.5rem 0 0.5rem;
}

.player-wrapper .player-info {
  padding: 1rem;
  margin: 0;
}

.player-wrapper .player-info:first-of-type {
  width: 30%;
  margin-right: 1%;
}

.player-wrapper .player-info:last-of-type {
  width: 69%;
}

.player-wrapper .player-info > section {
  flex-direction: column;
}

select {
    background-color: transparent;
    border: none;
    padding: 0 1em 0 0;
    margin: 0;
    width: 100%;
    font-family: inherit;
    font-size: inherit;
    cursor: inherit;
    line-height: inherit;
    outline: none;
}

form {
    width: 100%;
    display: grid;
    grid-template-areas: "select";
    min-width: 15ch;
    max-width: 30ch;
    border-radius: 0.25em;
    padding: 0.25em 0.5em;
    font-size: 1.25rem;
    cursor: pointer;
    line-height: 1.1;
    background-color: #fff;
    margin-bottom: 2rem;
}

label {
    font-size: 1.125rem;
    font-weight: 500;
}

form + label {
    margin-top: 2rem;
}

.select {
  border: none;
  -webkit-appearance: none;
          appearance: none;
  background-color: white;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  width: 90%;
}

.select:focus {
  outline: none;
}

.wrapper .search {
  justify-content: flex-start;
}

.search {
  margin-bottom: 4rem;
}

.lc {
  border-radius: 5px;
  margin: 0.5rem;
  width: 100%;
}

.lc .member {
  margin: 0;
  width: unset;
  box-shadow: unset;
}

.lc i {
  margin: 1rem;
  cursor: pointer;
}

.float-right {
  float: right;
}

.collapsible table {
  width: 100%;
  margin-top: 16px;
}

.collapsible .player-info {
  padding-top: 0;
}

.collapsible h3 {
  margin: 0;
}

.collapsible tbody td {
  padding: 8px;
}

.collapsible tbody td p {
  margin: 4px 0 4px 0;
}

.collapsible .flex {
  margin: 0 auto;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  gap: 10px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Quicksand", sans-serif;
  margin: 0;
  background-color: #333;
}

a {
  text-decoration: none;
  color: black;
}

.logo {
  height: 70px;
  width: 80px;
}

.flex {
  display: flex;
}

.wrapper {
  width: 90%;
  margin: 4rem auto 2rem auto;
}

.pink,
.wrapper a:hover,
.wrapper .asLink:hover {
  color: #f040af;
}

